*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
  }
  .active{
    color: #ffffff;
    font-size: 18px;
    font-weight: 700;
    list-style: none;
    background: #49494985;
    border-radius: 10px;
    text-decoration: none;
  }
  @media screen and (max-width:765px) {
    .active{
      border-radius: 0;
    }
  }